.header__logo {
    padding: 5px;

}

.navigation {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    

    /* margin-right: auto; */
}

.navigation__list {
    display: flex;
    list-style: none;
    color: black;
    margin-bottom: 0;
    flex-wrap: wrap;
    padding: 0;
    /* margin-right: auto; */
}

.navigaton__item {
    padding: 5px;

}

.navigaton__link {
    text-decoration: none;
    color: black;

}

.header__user {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    padding-left: 5%;
    /* height:20%; */
    /* margin-left: auto; */
}

.header__room {
    margin-right: 10px;
    background-color: rgb(212, 91, 46);
    padding: 3px 7px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

}

.header__user_class {
    display: block;
    border-radius: 50%;
    
z-index: 2;
    /* position: relative; */
}


