.header__progress {
    width: 13%;
height: 10%;
    justify-content: center;
    position: relative;
    display: block;
    padding:2%;
}
.header__frame{
  position: absolute;
  width: 200%;
  height: 100%;
  top:-15%;
  left : -20%;
}

/* .header__progress :nth-child(:all) {
  border-radius:10%;
} */
.header__user_params {
    display: grid;
    align-items: center;
    grid-gap: 3px;
}

.bag{
  width: 4%;
  /* height: 10%; */
  display: block;
  filter: drop-shadow(0px 0px 15px rgb(20, 20, 20));
  transition: 200ms;
  position: absolute;
  left: 35%;
}

.bag:hover{
  filter: drop-shadow(0px 0px 15px rgb(200, 68, 16));
  transition: 200ms;
  width: 6%;
  /* wi */
}
.header__exp{
  filter: drop-shadow(0px 10px 8px rgb(135, 97, 82));
transition: 500ms;
border-radius: 10%;
}


