.modal {
 height: 100vh; 
 width: 100vw;
 background-color: rgba(0,0,0,0,0.4);
 position: fixed;
 top: 0;
 left: 0;
 display: flex;
 align-items: center;
 justify-content: center;
 opacity: 0;
 pointer-events: none;
 transition: 0.5s;
}


.modal.active {
  opacity: 1;
/* transform: scale(1); */
pointer-events: all;
background-color:rgba(0,0,0,0.8)
}

.modal__content {
padding: 20px;
border-radius: 12px;
background-color: lightblue;
height: 80%;
max-height: 800px;
width: 80%;
max-width: 800px;
transform: scale(1);
transition: 0.4s all;
}


#lalala {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 30vh;
  
}

